export function Table<T extends object>(props: TableProps<T>): React.ReactElement {
  const {
    // localStorageStateKey,
    frozenHeader,
    height,
    columns,
    data,
    onPaginationChange,
    onSortChange,
    onRowClick,
    pagination,
    sort,
    filters,
    filterable,
    isLoading,
    rowsPerPageOptions = [10, 15, 20, 25],
    title,
    topic,
    onClearFilters,
    // onClick,
  } = props

  // const classes = useStyles()
  // const initialSortState = columns.map(column => )
  const [sortState, setSortState] = useState<SortState<T>>(sort || {});
  // const [filterState, setFilterState] = useState<FilterState<T>>(filters || {});
  const [paginationState, setPaginationState] = useState<PaginationProps>(pagination || defaultPaginationState);
  const [filtersDrawerOpen, setFiltersDrawerOpen]= useState(false);

  useEffect(() => {
    if (pagination) {
      setPaginationState(pagination)
    }
  }, [pagination])

  const handleChangePage = useCallback((_evt: unknown, newPage: number) => {
    const newPaginationState: PaginationProps = {
      ...paginationState,
      current: newPage + 1,
    }
    setPaginationState(newPaginationState);
    onPaginationChange && onPaginationChange(newPaginationState)
  }, [paginationState, onPaginationChange]);

  const handleChangeRowsPerPage = useCallback((evt: React.ChangeEvent<HTMLInputElement>) => {
    const newPaginationState: PaginationProps = {
      ...paginationState,
      pageSize: +evt.target.value,
      current: 1,
    }
    setPaginationState(newPaginationState);
    onPaginationChange && onPaginationChange(newPaginationState);
  }, [paginationState, onPaginationChange]);;

  const handleRowClick = useCallback((record: T, evt: React.MouseEvent<HTMLTableRowElement, MouseEvent>) => {
    if (onRowClick) {
      onRowClick(record, evt);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onRowClick]);

  const handleFilterClick = () => {
    setFiltersDrawerOpen(true);
  };

  const rowClassName = useMemo(() => {
    return !!onRowClick ? TableRowClickableStyles : undefined;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onRowClick])

  const tableHeaders = useMemo(() => (
    <TableRow>
      {columns.map((column, index) => {
        const columnSortState = sortState[column.dataIndex];
        const columnDataIndex = column.dataIndex;
        return (
          <TableCell
            classes={{
              root: TableHeadCellStyles,
            }}
            // className={clsx(
            //   // classes.tableHeadCell,
            //   {[classes.tableHeadCellSortable]: column.sortable},
            // )}
            width={column.width}
            key={index}
          >
            {
              column.sortable ?
              <TableSortLabel
                className={TableHeadCellInnerStyles}
                active={!!columnSortState}
                direction={columnSortState ? columnSortState === 'descend' ? 'desc' : 'asc' : undefined}
                onClick={() => {
                  let newSortState: SortState<T> = {};
                  if (columnSortState !== 'descend') {
                    newSortState = {
                      [columnDataIndex]: !columnSortState ? 'ascend' : 'descend',
                    } as SortState<T>;
                  }
                  setSortState(newSortState)
                  onSortChange && onSortChange(newSortState)
                }}
              >
                {column.title}
              </TableSortLabel> :
              <span className={TableHeadCellInnerStyles}>{column.title}</span>
            }
          </TableCell>
          )
        }
      )}
    </TableRow>
  // eslint-disable-next-line react-hooks/exhaustive-deps
  ), [columns, onSortChange, sortState]);

  const rowCells = useMemo(
    () =>
      data?.map((row: T, i) => {
        return (
          <TableRow
            key={i}
            onClick={(evt: React.MouseEvent<HTMLTableRowElement, MouseEvent>) => {
              handleRowClick(row, evt);
            }}
            className={rowClassName}
          >
            {columns.map((column, index) => 
              <TableCell className={TableCellStyles} key={index}>
                {column.render ? column.render(((row as any)[column.dataIndex]), row, index) : ((row as any)[column.dataIndex])}
              </TableCell>
            )}
          </TableRow>
        )
      }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [columns, data, handleRowClick, rowClassName]
  );

  const filterComponents = useMemo(
    () => (
      columns.map((column, index) => (
        column.filterComponent &&
        <React.Fragment key={index}>{column.filterComponent}</React.Fragment>
      ))
    ),
    [columns],
  )

  const hasFilters = useMemo(
    () => !isEmptyObject(filters),
    [filters],
  )

  return (
    <>
      {
        (title || filterable) &&
        <Toolbar variant="dense">
          <h2>{title}</h2>
          {
            filterable &&
            <Tooltip title="Filter">
              <IconButton
                onClick={handleFilterClick}
                aria-label="filter list"
                color={hasFilters ? "primary" : undefined}
              >
                <FilterListIcon />
              </IconButton>
            </Tooltip>
          }
        </Toolbar>
      }
      <TableLoaderContainer>{ isLoading && <DelayedLoader />}</TableLoaderContainer>
      <TableContainer style={{ maxHeight: height }}>
        <MuiTable stickyHeader={frozenHeader}>
          <TableHead>{tableHeaders}</TableHead>
          <TableBody>
            {rowCells}
          </TableBody>
        </MuiTable>
      </TableContainer>
      {
        (!!pagination) &&
        <TablePagination
          rowsPerPageOptions={rowsPerPageOptions}
          count={pagination.total}
          rowsPerPage={pagination.pageSize}
          page={pagination.current - 1}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      }
      {
        (filterable && onClearFilters) &&
        <FilterDrawer
          filteredCount={pagination?.total || 0}
          topicName={topic}
          isOpen={filtersDrawerOpen}
          onClose={() => setFiltersDrawerOpen(false)}
          onClearFilters={onClearFilters}
        >
          {filterComponents}
        </FilterDrawer>
      }
    </>
  )
}
