import React, { PropsWithChildren, useCallback } from 'react';
import {
  Button,
  createStyles,
  DialogActions,
  Drawer,
  // Grid,
  makeStyles,
  ModalProps
  } from '@material-ui/core';

const useStyles = makeStyles((theme) =>
  createStyles({
    filterDrawer: {
    },
    filterHeading: {
      padding: theme.spacing(2),
      margin: 0,
      borderBottom: '1px solid',
      borderBottomColor: theme.palette.grey[200]
    },
    filters: {
      padding: theme.spacing(2),
      minHeight: '100%',
    },
    filterDrawerContent: {
      minWidth: 250,
      display: 'grid',
      height: '100%',
      gridTemplateRows: 'auto 1fr auto',
    },
    filterDrawerBackdrop: {
      background: 'rgba(0, 0, 0, 0.1)',
    },
    filteredCount: {
      fontSize: '0.8em',
      color: theme.palette.grey[400],
      marginLeft: theme.spacing(1)
    },
    filterFooter: {
      borderTop: '1px solid',
      borderTopColor: theme.palette.grey[200]
    },
  }),
);

interface FilterDrawerProps {
  isOpen: boolean;
  filteredCount: number;
  topicName?: string;
  isLoading?: boolean;
  onClose: ModalProps['onClose'];
  onClearFilters: () => void;
}

const FilterDrawer: React.FC<PropsWithChildren<FilterDrawerProps>> = ({
  isOpen,
  onClose,
  onClearFilters,
  children,
  topicName,
  // isLoading,
  filteredCount,
}) => {
  const classes = useStyles()

  const handleClear = useCallback((evt: any) => {
    if (onClearFilters) {
      onClearFilters();
    }
    if (onClose) {
      onClose(evt, 'escapeKeyDown');
    }
  }, [onClearFilters, onClose]);

  const handleApply = useCallback((evt: any) => {
    if (onClose) {
      onClose(evt, 'escapeKeyDown');
    }
  }, [onClose]);

  return (
    <Drawer
      anchor="right"
      className={classes.filterDrawer}
      open={isOpen}
      onClose={onClose}
      // transitionDuration={100}
      ModalProps={{
        BackdropProps: {
          className: classes.filterDrawerBackdrop
        }
      }}
    >
      <div className={classes.filterDrawerContent}>
        <h2 className={classes.filterHeading}>Filter <span className={classes.filteredCount}>{filteredCount} {topicName?.toLowerCase()}</span></h2>
        <div className={classes.filters}>{children}</div>
        <div className={classes.filterFooter}>
          <DialogActions>
            {/* <Grid container spacing={3}>
              <Grid item xs={6}>
                <Button size="large" autoFocus color="primary" onClick={handleClear}>
                  Clear
                </Button>
              </Grid>
              <Grid item xs={6} className>
                <Button size="large" onClick={handleApply} variant="contained" color="primary">
                  Apply
                </Button>
              </Grid>
            </Grid> */}
            <Button size="large" color="primary" onClick={handleClear}>
              Clear
            </Button>
            <Button size="large" onClick={handleApply} variant="contained" color="primary">
              Apply
            </Button>
          </DialogActions>
        </div>
      </div>
    </Drawer>
  );
}

export default FilterDrawer;
